<template>
  <view class="real-auth">
    <div class="myIdentityBox" v-if="isAuth">
      <view class="identityItem" v-for="(item, index) in roleList" :key="index + 'identity'">
        <view class="tagIcon">{{ item.resource_type_name }}</view>
        <div class="top">
          <view>{{ item.name }}</view>
          <view>{{ item.phone }}</view>
        </div>
        <div class="formItem" v-if="item.sport_talent_type">
          <div class="label">人才类型:</div>
          <div class="content">{{ item.sport_talent_type }}</div>
        </div>
        <div class="formItem" v-if="item.site">
          <div class="label">站点:</div>
          <div class="content">{{ item.site || '-' }}</div>
        </div>
        <div class="formItem" v-if="item.organization || item.resource_name">
          <div class="label">所属组织:</div>
          <div class="content">{{ item.organization || item.resource_name }}</div>
        </div>
      </view>
    </div>
    <div class="unAuthBox" v-else>
      <div class="icon">
        <image
          src="https://cdn-static.papa.com.cn/social/sportsService/realAuth.png"
          mode="widthFix"
        />
      </div>
      <div class="desc">未实名认证无法获取身份信息，请前往实名认证！</div>
      <div class="btn">
        <bottom-button @click="handleGotoRealAuth">去实名</bottom-button>
      </div>
    </div>
    <div class="btm">
      若您是站点管理员、体育人才（社体指导员、裁判员、运动员等）或体育组织管理员，
      尚未获取身份信息，请联系中心负责人确认。
    </div>
  </view>
</template>

<script>
import { getPersonnelAuth } from '@/apis/sportsService/common'
import bottomButton from '@/components/bottomButton.vue'
import themeMixins from '@/mixins/themeMixins.js'
export default {
  mixins: [themeMixins],
  name: 'realAuth',
  components: {
    bottomButton,
  },
  data() {
    return {
      isAuth: false,
      roleList: [],
    }
  },
  onLoad() {
    this.setNavigationBarColor()
  },
  onShow() {
    const app = getApp()
    let { navBarHeight, menuTop, userInfo } = app.globalData
    console.log({ userInfo })
    this.isAuth = userInfo.is_auth
    if (this.isAuth) {
      this.getIdentityList()
      //  uni.
    }
  },
  methods: {
    handleGotoRealAuth() {
      uni.navigateTo({
        url: './realAtuthSubmit',
      })
    },
    async getIdentityList() {
      const res = await getPersonnelAuth()
      if (res.code === 200) {
        console.log({ res })
        this.roleList = res.data
      }
    },
  },
}
</script>

